:root {
  --font-body: font-family: "Rubik", sans-serif;
}

:root {
  --black: hsl(0, 0%, 0%);
  /* Text */
  --pale-magenta: hsl(293, 100%, 91%);
  --pale-violet: hsl(276, 100%, 81%);
  --p-violet: hsl(276, 100%, 95%);
  --mod-violet: hsl(276, 55%, 52%);
  --dk-violet: hsl(271, 15%, 43%);
  --grayish-blue: hsl(206, 6%, 79%);
  --vry-dk-violet: hsl(271, 36%, 24%);
  --dk-gr-violet: hsl(270, 7%, 64%);
  /* Gradients */
  --lt-magenta: hsl(293, 100%, 63%);
  --lt-violet: hsl(264, 100%, 61%);
  /* Secondary */
  --white: hsl(0, 0%, 100%);
  --lt-gr-violet: hsl(270, 20%, 96%);
  --vry-lt-magenta: hsl(289, 100%, 72%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  background-color: var(--dk-gr-violet);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 4rem 0;
  max-width: 69.375rem;
  margin: 0 auto;
}

.app {
  position: relative;
  width: 16.25rem;
  border: 0.625rem solid var(--white);
  border-radius: 1.875rem;
}
.app::before {
  content: "";
  width: 8.125rem;
  height: 1.4375rem;
  border-radius: 0 0 0.9375rem 0.9375rem;
  background-color: var(--white);
  position: absolute;
  left: 50%;
  top: -1%;
  transform: translateX(-50%);
}

.header {
  display: flex;
  align-items: center;
  gap: 0.625rem;
  border-radius: 1.25rem 1.25rem 0.3125rem 0.3125rem;
  padding: 1.875rem 0.9375rem 0.8125rem;
  color: var(--p-violet);
  background-image: linear-gradient(to right, var(--lt-violet), var(--lt-magenta));
}
.header__arrow {
  color: var(--p-violet);
}
.header__avatar {
  font-size: 0;
}
.header__avatar img {
  border: 0.125rem solid var(--white);
  border-radius: 50%;
}
.header__username {
  flex: 1;
  font-size: 0.625rem;
  font-weight: 700;
}
.header__username span {
  font-size: 0.5rem;
  color: var(--pale-violet);
}
.header__dots {
  width: 0.625rem;
  color: var(--p-violet);
}

.message {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0.75rem 0.5rem;
  font-size: 0.625rem;
  font-weight: 500;
  background-color: var(--lt-gr-violet);
}
.message > * {
  margin-bottom: 0.5rem;
}
.message > :nth-child(2), .message :nth-child(5), .message :nth-child(9) {
  margin-bottom: 1rem;
}
.message form {
  width: 0;
  height: 0;
  margin: 0;
}
.message__left {
  color: var(--mod-violet);
  background-color: #e6e0eb;
  padding: 0.5rem;
  width: 62%;
  border-radius: 0.625rem 0.625rem 0.625rem 0.1875rem;
}
.message__right {
  align-self: flex-end;
  padding: 0.5rem;
}
.message__right--img {
  padding: 0;
}
.message__right--img :not(:last-child) {
  margin-right: 0.3125rem;
}
.message__right--txt {
  max-width: 62%;
  color: var(--dk-violet);
  background-color: var(--white);
  border-radius: 0.625rem 0.625rem 0 0.625rem;
}
.message__right img {
  border-radius: 0.625rem;
  width: 2.5rem;
  height: 2.5rem;
}
.message__book {
  display: flex;
  align-items: center;
  color: var(--white);
  width: 75%;
  padding: 0.625rem 1rem 0.625rem 0.625rem;
  background-image: linear-gradient(to right, var(--lt-magenta), var(--lt-violet));
  border-radius: 0.8125rem 0.8125rem 0.8125rem 0.1875rem;
}
.message__book--mmcradio {
  width: 0.875rem;
  height: 0.875rem;
  border: 0.125rem solid var(--vry-lt-magenta);
  border-radius: 50%;
  cursor: pointer;
}
.message__book--radio {
  margin: 0;
  width: 0.0625rem;
  height: 0.0625rem;
  visibility: hidden;
}
.message__book label {
  flex: 1;
  cursor: pointer;
  color: var(--pale-magenta);
}
.message__book span {
  font-size: 0.875rem;
  font-weight: 700;
}